.friendList {
  list-style: none;
  margin: 50px 0 0 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.friendList__item {
  width: 300px;
  background-color: #fff;
  margin-bottom: 15px;
  display: flex;
  padding: 10px;
  align-items: center;
  box-shadow: 5px 4px 10px -3px #000000;
  border-radius: 5px;
}

.friendList__status {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 15px;
}

.friendList__image {
  margin-right: 15px;
}

.friendList__name {
  font-size: 18px;
  font-weight: 600;
}
