.user__profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 30px auto 0 auto;
  padding-top: 15px;
  width: 300px;
  text-align: center;
  background-color: #fff;
  box-shadow: 5px 4px 10px -3px #000000;
  min-height: 340px;
}

.user__image {
  width: 150px;
  border-radius: 50%;
}

.user__name {
  font-weight: 700;
  font-size: 16px;
}

.user__secondaryInfo {
  font-size: 14px;
  color: rgb(166, 166, 166);
}

.user__stats {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: rgb(210, 217, 236);
}

.user__statsItem {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 33.3%;
  padding: 15px;
  color: rgb(166, 166, 166);
}

.user__quantity {
  color: black;
  font-weight: 700;
}
